<template>
    <layout-page>
        <div class="card-header">
            <div class="row">
                <div class="col-12 ps-4 pt-3 ">
                    <div class="float-start">
                        <h5>Editar Unidades</h5>
                    </div>
                    <div class="float-end">
                        <button-widget cor="azul" href="../index" tamanho="M">
                            Voltar
                        </button-widget>
                    </div>
                </div>

            </div>
        </div>
        <div class="card-body">
            <div class="row">
                <FormUserUnidades></FormUserUnidades>
                <IndexUserUnidadeAnexos></IndexUserUnidadeAnexos>
                    <div class="col-4">
                        <button class="btn btn-primary mt-4" type="button" @click="sendForm">Salvar</button>
                    </div>
            </div>
           
        </div>
    </layout-page>

</template>

<script>
import FormUserUnidades from "@/views/user_unidades/FormUserUnidades.vue";
import ButtonWidget from "@/components/widget/buttonWidget.vue";
import LayoutPage from "@/components/page/layoutPage.vue";
import toastr from "toastr/build/toastr.min";
import user_unidadeService from "@/services/user_unidade.service";
import IndexUserUnidadeAnexos from "../user_unidade_anexos/IndexUserUnidadeAnexos.vue";

export default {
    name: "EditUserUnidades",
    components: {LayoutPage, ButtonWidget, FormUserUnidades,IndexUserUnidadeAnexos},
    methods:{
        async edit(id){
            let user_unidadesService = new user_unidadeService();
            let response = await user_unidadesService.view(id);
        document.getElementById('nome').value = response.data.nome;
document.getElementById('descritivo').value = response.data.descritivo;
document.getElementById('cidade_id').value = response.data.cidade_id;
document.getElementById('cnpj').value = response.data.cnpj;

        },
        async sendForm(){
            let dataForm = {
        nome: document.getElementById('nome').value,
descritivo: document.getElementById('descritivo').value,
cidade_id: document.getElementById('cidade_id').value,
cnpj: String(document.getElementById('cnpj').value).replace(/[^a-zA-Z0-9]/g, ''),

            _method:'PUT'

        }
            if(!dataForm.parent_id){
                delete dataForm.parent_id
            }
            let id = this.$route.params.id;
            let user_unidadesService = new user_unidadeService();
            let response = await user_unidadesService.update(dataForm,id);
            if(response.data?.id){
                toastr.success('Salvo com sucesso')
            }else{
                if (response.response.data?.message){
                    toastr.error(response.response.data?.message);
                }else{
                    toastr.error('Houve um problema ao inserir');
                }

            }
        }
    },
    created() {
        this.edit(this.$route.params.id)
    }
}
</script>

