<template>
  <layout-page>
    <div class="row">
      <div class="col-12">
        <div class="row">
          <div class="col-12 ps-4 pt-3 ">
            <div class="float-start">
              <h5> Clientes</h5>
            </div>
            <div class="w-50">
              <input id="search" class="form-control" @change="list()" placeholder="Digite sua pesquisa"
                     type="text" v-model="search">
            </div>

            <div class="float-end">
              <button-widget cor="azul" href="/clientes/create" tamanho="M">
                Adicionar
              </button-widget>
            </div>
          </div>

        </div>
        <table class="table">
          <thead>
          <tr>
            <th>Aes</th>
            <th></th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="row in rows" :key="row.id">
            <td>
              <div class="dropdown">
                <a class="btn btn-primary btn-sm dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown"
                   aria-expanded="false">
                  Aes
                </a>

                <ul class="dropdown-menu">
                  <li>
                    <router-link class="dropdown-item" :to="'/clientes/edit/'+row.id">
                      <i class="bi bi-pencil-square"></i>
                      Editar
                    </router-link>
                  </li>
                  <li>
                        <span class="dropdown-item cursor-pointer" @click="deleteRow(row.id)">
                           <i class="bi bi-trash2-fill"></i>
                          Apagar
                        </span>
                  </li>

                </ul>
              </div>


            </td>

            <td>
              <div class="col-12"><strong>Nome : </strong>{{ row.nome }}</div>
              <div class="col-12"><strong>CPF : </strong>{{ row.cpf }}</div>
              <div class="col-12"><strong>CNPJ : </strong>{{ row.cnpj }}</div>
              <div class="col-12"><strong>E-mail : </strong>{{ row.email }}</div>
            </td>
            <td>
              <div class="col-12"><strong>Telefone : </strong>{{ row.telefone }}</div>
              <div class="col-12"><strong>Celular : </strong>{{ row.celular }}</div>
              <div class="col-12"><strong>Tipo : </strong>{{ row.tipo }}</div>
              <div class="col-12"><strong>Ativo : </strong>{{ row.ativo?'sim':'no' }}</div>
              <div class="col-12"><strong>Categoria : </strong>{{ row.pessoa_categoria }}</div>
            </td>

          </tr>
          <tr v-if="rows==null ">
            <td colspan="2">
              <div class="row">
                <div class="col-12 d-flex justify-content-center">
                  <div class="loader"></div>
                </div>
              </div>
            </td>
          </tr>
          <tr v-if="rows==false ">
            <td colspan="2" class="text-center"> No h dados</td>
          </tr>
          </tbody>
        </table>

      </div>
    </div>


  </layout-page>
</template>
<script>
import LayoutPage from "@/components/page/layoutPage.vue";
import ButtonWidget from "@/components/widget/buttonWidget.vue";
import toastr from "toastr/build/toastr.min";
import Helpers from "@/services/Helpers";
import pessoaService from "@/services/pessoa.service";

export default {
  name: "IndexClientes",
  components: {ButtonWidget, LayoutPage},
  data() {
    return {
      rows: null,
      search: null
    }
  },
  methods: {
    async list() {

      let pessoasService = new pessoaService();

      let dataRow = await pessoasService.list(this.search, 'cliente');
      let helpers = new Helpers();

      if (dataRow.data.data.length > 0) {
        this.rows = dataRow.data.data;

      } else if (!helpers.empty(dataRow.response?.data)) {
        toastr.error('Houve um problema');
      } else {

        this.rows = false;
      }


    },
    async deleteRow(id) {
      let pessoasService = new pessoaService();
      let dataRow = await pessoasService.delete(id);
      if (dataRow.data.success) {
        this.list();
        toastr.success('Apagado com sucesso');
      } else {
        toastr.error('Houve um problema ao apagar');
      }
    }

  },
  created() {
    this.list();

  }
}

</script>

<style scoped>
@import "toastr/build/toastr.css";
@import "bootstrap-icons/font/bootstrap-icons.min.css";
</style>
