<template>
  <layout-page>
    <div class="card-header">
      <div class="row">
        <div class="col-12 ps-4 pt-3 ">
          <div class="float-start">
            <h5>Editar Categorias</h5>
          </div>
          <div class="float-end">
            <button-widget cor="azul" href="../index" tamanho="M">
              Voltar
            </button-widget>
          </div>
        </div>

      </div>
    </div>
    <div class="card-body">
      <div class="row">
        <FormPessoaCategorias></FormPessoaCategorias>
        <div class="col-4">
        <button class="btn btn-primary mt-4" type="button" @click="sendForm">Salvar</button>
        </div>
      </div>
    </div>
  </layout-page>

</template>

<script>
import FormPessoaCategorias from "@/views/pessoa_categorias/FormPessoaCategorias.vue";
import ButtonWidget from "@/components/widget/buttonWidget.vue";
import LayoutPage from "@/components/page/layoutPage.vue";
import toastr from "toastr/build/toastr.min";
import pessoa_categoriaService from "@/services/pessoa_categoria.service";

export default {
  name: "EditPessoaCategorias",
  components: {LayoutPage, ButtonWidget, FormPessoaCategorias},
  methods: {
    async edit(id) {
      let pessoa_categoriasService = new pessoa_categoriaService();
      let response = await pessoa_categoriasService.view(id);
      document.getElementById('nome').value = response.data.nome;
      document.getElementById('descritivo').value = response.data.descritivo;
      document.getElementById('tipo').value = response.data.tipo;
      document.getElementById('ativo').value = response.data.ativo;

    },
    async sendForm() {
      let dataForm = {
        nome: document.getElementById('nome').value,
        descritivo: document.getElementById('descritivo').value,
        tipo: document.getElementById('tipo').value,
        ativo: document.getElementById('ativo').value,

        _method: 'PUT'

      }
      if (!dataForm.parent_id) {
        delete dataForm.parent_id
      }
      let id = this.$route.params.id;
      let pessoa_categoriasService = new pessoa_categoriaService();
      let response = await pessoa_categoriasService.update(dataForm, id);
      if (response.data?.id) {
        toastr.success('Salvo com sucesso')
      } else {
        if (response.response.data?.message) {
          toastr.error(response.response.data?.message);
        } else {
          toastr.error('Houve um problema ao inserir');
        }

      }
    }
  },
  created() {
    this.edit(this.$route.params.id)
  }
}
</script>

