<template>
  <layout-page>
    <div class="card-header">
      <div class="row">
        <div class="col-12 ps-4 pt-3 ">
          <div class="float-start">
            <h5>Editar Propostas</h5>
          </div>
          <div class="float-end">
            <button-widget cor="azul" href="../index" tamanho="M">
              Voltar
            </button-widget>
          </div>
        </div>

      </div>
    </div>
    <div class="card-body">
      <div class="row">
        <FormPedidoPropostas></FormPedidoPropostas>
        <div class="col-4">
          <button class="btn btn-primary mt-4" type="button" @click="sendForm">Salvar</button>
        </div>
      </div>
    </div>
  </layout-page>

</template>

<script>
import FormPedidoPropostas from "@/views/pedido_propostas/FormPedidoPropostas.vue";
import ButtonWidget from "@/components/widget/buttonWidget.vue";
import LayoutPage from "@/components/page/layoutPage.vue";
import toastr from "toastr/build/toastr.min";
import pedido_propostaService from "@/services/pedido_proposta.service";

export default {
  name: "EditPedidoPropostas",
  components: {LayoutPage, ButtonWidget, FormPedidoPropostas},
  methods: {
    async edit(id) {
      let pedido_propostasService = new pedido_propostaService();
      let response = await pedido_propostasService.view(id);
      document.getElementById('descritivo').value = response.data.descritivo;
      document.getElementById('ativo').value = response.data.ativo;
      document.getElementById('produto_id').value = response.data.produto_id;
      document.getElementById('pessoa_id').value = response.data.pessoa_id;

    },
    async sendForm() {
      let dataForm = {
        descritivo: document.getElementById('descritivo').value,
        ativo: document.getElementById('ativo').value,
        pessoa_id: document.getElementById('pessoa_id').value,

        _method: 'PUT'

      }
      if (!dataForm.parent_id) {
        delete dataForm.parent_id
      }
      let id = this.$route.params.id;
      let pedido_propostasService = new pedido_propostaService();
      let response = await pedido_propostasService.update(dataForm, id);
      if (response.data?.id) {
        toastr.success('Salvo com sucesso')
      } else {
        if (response.response.data?.message) {
          toastr.error(response.response.data?.message);
        } else {
          toastr.error('Houve um problema ao inserir');
        }

      }
    }
  },
  created() {
    this.edit(this.$route.params.id)
  }
}
</script>

