<script>


import PainelService from "@/services/painel.service";

export default {
  name: "DashBoardPage",

  components: {},
  data() {
    return {
      contatos: '0,00',
      labelContatos: '',
      acessos: '0',
      naoResolvidos: '0',
      labelAcessos:'',
      labelNaoResolvidos:'',
      visitas:null
    }
  },
  methods: {
    async loadTotalizadores() {
      let painelService = new PainelService(process.env.VUE_APP_API_HOST_NAME);
      this.contatos  = await painelService.contatos();

      this.acessos = await  painelService.acessos();

      this.naoResolvidos  = await painelService.naoResolvido();
    },
    nomeEUltimoDiaDoMes() {
      // Obtm a data atual
      var dataAtual = new Date();

      // Obtm o nmero do ms (0 - janeiro, 1 - fevereiro, etc.)
      var numeroMes = dataAtual.getMonth();

      // Array com o nome dos meses
      var nomesMeses = ['Janeiro', 'Fevereiro', 'Maro', 'Abril', 'Maio', 'Junho', 'Julho', 'Agosto', 'Setembro', 'Outubro', 'Novembro', 'Dezembro'];

      // Obtm o nome do ms atual
      var nomeMesAtual = nomesMeses[numeroMes];

      // Obtm o ltimo dia do ms atual
      var ultimoDia = new Date(dataAtual.getFullYear(), numeroMes + 1, 0).getDate();

      // Retorna um objeto com o nome do ms atual e o ltimo dia do ms
      this.labelContatos = '1 at o dia '+ultimoDia+' de '+nomeMesAtual;
      this.labelAcessos = '1 at o dia '+ultimoDia+' de '+nomeMesAtual;
      this.labelNaoResolvidos = '1 at o dia '+ultimoDia+' de '+nomeMesAtual;
    },
    async listVisitas(){
      let painelService = new PainelService(process.env.VUE_APP_API_HOST_NAME);
      this.visitas  = await painelService.list();
    }
  },
  created() {
  },
  mounted() {
    this.loadTotalizadores()
    this.nomeEUltimoDiaDoMes()
    this.listVisitas()
  }

}


</script>

