<template>
  <layout-page>
    <div class="row">
      <div class="col-12">
        <div class="row">
          <div class="col-12 ps-4 pt-3 ">
            <div class="float-start">
              <h5>Questes</h5>
            </div>
            <div class="w-50">
              <input id="search" class="form-control" @change="list()" placeholder="Digite sua pesquisa"
                     type="text" v-model="search">
            </div>
            <div class="float-end m-2">
              <button-widget cor="azul" :href="'/produto_questionarios/index/'+produto_id" tamanho="M">
                Voltar
              </button-widget>
            </div>
            <div class="float-end m-2">
              <button-widget cor="azul" :href="'/produto_perguntas/create/'+questionario_id" tamanho="M">
                Adicionar
              </button-widget>
            </div>

          </div>

        </div>
        <table class="table">
          <thead>
          <tr>
            <th>Aes</th>
            <th></th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="row in rows" :key="row.id">
            <td>
              <div class="dropdown">
                <button class="btn btn-system btn-sm dropdown-toggle" type="button" data-bs-toggle="dropdown"
                        aria-expanded="false">
                  Aes
                </button>
                <ul class="dropdown-menu">
                  <li>
                    <router-link class="dropdown-item" :to="'/produto_perguntas/edit/'+row.id">
                      <i class="bi bi-pencil-square"></i>
                      Editar
                    </router-link>
                  </li>
                  <li>
                     <span class="dropdown-item cursor-pointer" @click="deleteRow(row.id)">
                         <i class="bi bi-trash2-fill"></i>
                            Apagar

                     </span>
                  </li>
                </ul>
              </div>


            </td>

            <td>
              <div class="col-12"><strong>Enunciado : </strong>{{ row.enunciado }}</div>
              <div class="col-12"><strong>Tipo : </strong>{{ row.tipo }}</div>
              <div class="col-12"><strong>Obrigatrio : </strong>{{ row.obrigatorio ? 'sim' : 'no' }}</div>
              <div class="col-12"><strong>Ordem : </strong>{{ row.ordem }}</div>
            </td>
            <td>
              <div class="col-12"><strong>Ativo : </strong>{{ row.ativo ? 'sim' : 'no' }}</div>
            </td>

          </tr>
          <tr v-if="rows==null ">
            <td colspan="2">
              <div class="row">
                <div class="col-12 d-flex justify-content-center">
                  <div class="loader"></div>
                </div>
              </div>
            </td>
          </tr>
          <tr v-if="rows===false ">
            <td colspan="2" class="text-center"> No h dados</td>
          </tr>
          </tbody>
        </table>

      </div>
    </div>


  </layout-page>
</template>
<script>
import LayoutPage from "@/components/page/layoutPage.vue";
import ButtonWidget from "@/components/widget/buttonWidget.vue";
import toastr from "toastr/build/toastr.min";
import Helpers from "@/services/Helpers";
import produto_perguntaService from "@/services/produto_pergunta.service";
import produto_questionarioService from "@/services/produto_questionario.service";


export default {
  name: "IndexProdutoPerguntas",
  components: {ButtonWidget, LayoutPage},
  data() {
    return {
      rows: null,
      search: null,
      questionario_id: null,
      produto_id: null
    }
  },
  methods: {
    async list(id) {
      this.questionario_id = id;
      let produto_perguntasService = new produto_perguntaService();
      let dataRow = await produto_perguntasService.list(this.search);
      let helpers = new Helpers();

      if (dataRow.data.data.length > 0) {
        this.rows = dataRow.data.data;

      } else if (!helpers.empty(dataRow.response?.data)) {
        toastr.error('Houve um problema');
      } else {
        this.rows = false;
      }


    },
    async deleteRow(id) {
      let produto_perguntasService = new produto_perguntaService();
      let dataRow = await produto_perguntasService.delete(id);
      if (dataRow.data.success) {
        this.list();
        toastr.success('Apagado com sucesso');
      } else {
        toastr.error('Houve um problema ao apagar');
      }
    },
    async findProdutoId(id) {
      let produto_questionariosService = new produto_questionarioService();
      let response = await produto_questionariosService.view(id);
      this.produto_id = response.data.produto_id;
    }

  },
  created() {
    this.list(this.$route.params.id);
    this.findProdutoId(this.$route.params.id)

  }
}

</script>

<style scoped>
@import "toastr/build/toastr.css";
@import "bootstrap-icons/font/bootstrap-icons.min.css";
</style>
